.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100vh;
  background: rgba(61, 51, 51, 0.9);
  z-index: 10000;
}

.modal__form {
  position: absolute;
  right: 0;
  left: 0;
  top: 18%;
  bottom: 0;
  margin: 0 auto;
  width: 480px;
  height: 485px;
  background-color: rgba(20, 20, 20, 1);
}

.modal__form h2 {
  margin: 0 0 36px;
  padding-top: 40px;
  padding-left: 33px;
  font-size: 32px;
  color: white;
}

.modal__toggle {
  position: absolute;
  width: 16px;
  height: 16px;
  right: 31px;
  top: 42px;
  background-image: url("../img/close.png");
  background-repeat: no-repeat;
  background-size: 14px 14px;
  background-color: transparent;
  border: none;
}

.modal__link {
  position: relative;
  display: block;
  width: 51%;
  margin-bottom: 44px;
  margin-left: 65px;
  letter-spacing: 0.5px;
  border-bottom: 1px solid white;
  color: white;


  &::before {
    content: "";
    position: absolute;
    left: -33px;
    width: 24px;
    height: 24px;
    background-image: url("../img/icon-gps-location.svg");
    background-repeat: no-repeat;
    background-size: 24px 24px;
  }
}

.modal__form span {
  display: block;
  margin-bottom: 20px;
  padding-left: 33px;
  font-size: 17px;
  color: white;
}

.modal__list {
  margin: 0;
  padding-left: 33px;
  font-size: 24px;
  font-weight: 600;
  list-style: none;
}

.modal__item {
  margin-bottom: 25px;
}

.modal__city-link {
  opacity: 0.5;
  color: rgb(255, 255, 255);
}
